.main{
    max-width: 80%;
    margin: auto;
    padding: 10px;
}
.txtarea{
    padding: 10px;
    width: 80%;
    resize: vertical;
    height: 300px;
    min-height: 300px;
    overflow-y: scroll;
    font-size: 18px;
    border: 2px solid rgb(77, 64, 64);
    border-radius: 2px;
    box-shadow: 3px 7px 6px 7px #888888;
}
ul.options li {
    display:inline;
}
li{
    margin: 0px 10px;
}
.button {
    background-color: #1e14ac; 
    border: none;
    color: white;
    padding: 15px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 8px;
  }

  .button:hover {
    background-color: #1c0edb; 
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
  }
  .errmsg{
      margin-top: 20px;
      margin-left: 50px;
      padding: 15px 50px;
      background-color:rgb(231, 90, 90);
      border-radius: 10px;
      width: 60%;
      color: seashell;
      text-align: left;
      font-size: 20px;
  }
  .msg{
    margin-top: 20px;
    margin-left: 50px;
    padding: 15px 50px;
    background-color:rgb(65, 202, 38);
    border-radius: 10px;
    width: 60%;
    color: seashell;
    text-align: left;
    font-size: 20px;
}
.processedView{
      padding: 10px;
      width: 80%;
      resize: vertical;
      height: 500px;
      min-height: 500px;
      overflow-y: scroll;
      box-shadow: 3px 7px 6px 7px #888888;
      font-size: 20px;
      color: rgb(0, 0, 94);
    }
    li{
        list-style-type: none;
        padding: 0px 20px;
    }

.togglebtn{
    font-size: 15px;
    background-color: rgb(170, 192, 235);
    border: 2px solid #1e14ac;
    border-radius: 9px;

}